<template>
    <v-content>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-alert type="warning" dismissible v-model="alert" class="mb-3">
                .<br>
               nembv site   .<br>
               (  )<br>
              id: guest / pwd: 123456   .
            </v-alert>
            <v-card class="elevation-12">
              <v-toolbar dark color="indigo">
                <v-icon>star</v-icon>
                <v-toolbar-title>
                  <v-tooltip bottom>
                    <span slot="activator">NEMVV </span>
                    <span>Node Express MongoDB Vue Vuetify<br> </span>
                  </v-tooltip>
                </v-toolbar-title>
                <v-spacer></v-spacer>
              </v-toolbar>
              <v-card-text>
                <v-form>
                  <v-text-field
                    prepend-icon="person"
                    label=""
                    v-model="form.id"
                    :counter="20"
                    :error-messages="errors.collect('form.id')"
                    v-validate="'required|min:4|max:20'"
                    data-vv-name="form.id"
                    required
                  ></v-text-field>
                  <v-text-field
                    prepend-icon="lock"
                    label=""
                    type="password"
                    v-model="form.pwd"
                    :error-messages="errors.collect('form.pwd')"
                    v-validate="'required|min:6|max:20|'"
                    data-vv-name="form.pwd"
                    required
                    @keyup.enter="submit"
                  ></v-text-field>
                  <v-checkbox
                    v-model="remember"
                    label="  (  )"
                    value="1"
                  >
                    <div slot="label">
                      <v-tooltip bottom>
                        <span slot="activator">  </span>
                        <span>7 .<br>  </span>
                      </v-tooltip>

                    </div>
                  </v-checkbox>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="success" @click="submit"></v-btn>
              </v-card-actions>
              <v-card-actions>
                <v-spacer></v-spacer>
                 ? <v-btn flat color="primary" @click="regist"></v-btn>
              </v-card-actions>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
</template>

<script>
  import ko from 'vee-validate/dist/locale/ko'

  export default {
    data: () => ({
      $_veeValidate: {
        validator: 'new'
      },
      path: 'auth/sign/in',
      alert: true,
      form: {
        id: '',
        pwd: ''
      },
      remember: null,
      // idRules: [
      //   v => !!v || ' ',
      //   v => (v && v.length >= 2) || ' 2    ',
      //   v => (v && v.length <= 20) || ' 20    ',
      // ],
      // pwdRules: [
      //   v => !!v || ' ',
      //   v => (v && v.length >= 4) || ' 4    ',
      //   v => (v && v.length <= 20) || ' 20    ',
      // ],
      dictionary: {
        messages: ko.messages,
        attributes: {
          'form.id': '',
          'form.pwd': ''
        },
        custom: {
          // name: {
          //   required: () => 'Name can not be empty',
          //   max: 'The name field may not be greater than 10 characters',
          //   // custom messages
          // },
        }
      }
    }),
    mounted () {
      this.$validator.localize('ko', this.dictionary)
    },
    methods: {
      swalSuccess (msg) {
        return this.$swal({
          icon: 'success',
          title: '',
          text: msg,
          timer: 2000
        })
      },
      swalWarning (msg) {
        return this.$swal({
          icon: 'warning',
          title: '',
          text: msg,
          timer: 2000
        })
      },
      swalError (msg) {
        return this.$swal({
          icon: 'error',
          title: '',
          text: msg,
          timer: 2000
        })
      },
      regist () {
        window.location.hash = '/register'
      },
      submit () {
        this.$validator.validateAll()
          .then((vr) => {
            if (!vr) throw new Error('   ')
            return this.$axios.post(`${this.$cfg.path.api}${this.path}`, this.form)
          })
          .then((res) => {
            if (!res.data.success) throw new Error(res.data.msg)
            if (!this.remember) this.$cookie.set('token', res.headers['www-authenticate'], { expires: this.$cfg.rememberNotTime })
            // return this.swalSuccess(`token: ${res.data.token}`)
            // return this.swalSuccess(`token: ${res.headers['www-authenticate']}`)
            this.$user._id = res.data.d._id
            this.$user.id = res.data.d.id
            this.$user.name = res.data.d.name
            this.$user.email = res.data.d.email
            return this.swalSuccess(' .  ')
          })
          .then(() => {
            window.location.hash = '/'
            // location.href = '/'
          })
          .catch((err) => {
            this.swalError(err.message)
          })
      }
    }
  }
</script>



